import React, { Component } from "react";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, Input, InputNumber, Modal, Col, Select } from "antd";
import getLanguage from "../../../App/MyLanguange"

const InputGroup = Input.Group;

const { Option } = Select;
// const { TextArea } = Input;

class ModalAddRaptorConnection extends Component {

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator } = this.props.form;
    const { locations, raptor_connection_location_array } = this.props.appConfig;
    // const error = pettyCashType.errorField ? pettyCashType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    // const formTextArea = {
    //   labelCol: {
    //     xs: { span: 24 },
    //     sm: { span: 24 }
    //   },
    //   wrapperCol: {
    //     xs: { span: 24 },
    //     sm: { span: 24 }
    //   }
    // };

    return (
      <Modal
        title={<IntlMessages id="addRaptorConnection" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="locations.pageHeader" />}
          >
            {getFieldDecorator("id_location", {
              rules: [
                {
                  required: true,
                  message: <IntlMessages id="required" />
                }
              ]
            })(
              <Select
                loading={this.props.appConfig.loading}
                showSearch
                optionFilterProp="children"
              //disabled={used ? true : false}
              >
                {locations.map((data, key) => (
                  raptor_connection_location_array.find(loc => loc === data.id_location)
                    ? null
                    :
                    (
                      <Option key={key} value={data.id_location}>
                        {data.location_code + " (" + data.location_name + ")"}
                      </Option>
                    )

                ))}
              </Select>
            )}
          </Form.Item>

          <InputGroup>
            <Col span={18}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="ipAddress" />}
              >
                {getFieldDecorator("ip_address", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                  ]
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="port" />}
              >
                {getFieldDecorator("db_port", {
                  rules: [
                  ]
                })(
                  <InputNumber style={{ width: "100%" }} />
                )}
              </Form.Item>
            </Col>
          </InputGroup>

          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="dbName" />}
          >
            {getFieldDecorator("db_name", {
              rules: [
                {
                  required: true,
                  message: <IntlMessages id="required" />
                }
              ]
            })(
              <Input />
            )}
          </Form.Item>

          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="username" />}
          >
            {getFieldDecorator("db_username", {
              rules: [
                {
                  required: true,
                  message: <IntlMessages id="required" />
                }
              ]
            })(
              <Input />
            )}
          </Form.Item>

          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="password" />}
          >
            {getFieldDecorator("db_password", {
            })(
              <Input.Password />
            )}
          </Form.Item>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddRaptorConnection = Form.create({ name: "modal_add_raptor_connection" })(
  ModalAddRaptorConnection
);

export default WrappedModalAddRaptorConnection;
